const CryptoKeyCache = new Map();
const JsonWebKeyCache = new WeakMap();
/**
 * Get a (cached) CryptoKey from JsonWebKey
 * @param algorithm - use which algorithm to import this key, defaults to ECDH K-256
 * @param key - The JsonWebKey
 * @param usage - Usage
 */
export async function JsonWebKeyToCryptoKey(key, usage = ['deriveKey'], algorithm = { name: 'ECDH', namedCurve: 'K-256' }) {
    const _key = JSON.stringify(key) + usage.join(',');
    if (CryptoKeyCache.has(_key))
        return CryptoKeyCache.get(_key);
    const cryptoKey = await crypto.subtle.importKey('jwk', key, algorithm, true, usage);
    CryptoKeyCache.set(_key, cryptoKey);
    JsonWebKeyCache.set(cryptoKey, key);
    return cryptoKey;
}
/**
 * Get a (cached) JsonWebKey from CryptoKey
 * @param key - The CryptoKey
 */
export async function CryptoKeyToJsonWebKey(key) {
    if (JsonWebKeyCache.has(key))
        return JsonWebKeyCache.get(key);
    const jwk = await crypto.subtle.exportKey('jwk', key);
    JsonWebKeyCache.set(key, jwk);
    CryptoKeyCache.set(JSON.stringify(jwk) + key.usages.join(','), key);
    return jwk;
}
