export function encodeText(str) {
    return new TextEncoder().encode(str);
}
export function decodeText(str) {
    return new TextDecoder().decode(str);
}
export function decodeArrayBuffer(str) {
    const decodedString = atob(str);
    const uintArr = [];
    for (const i of decodedString)
        uintArr.push(i.charCodeAt(0));
    return new Uint8Array(uintArr).buffer;
}
export function encodeArrayBuffer(buffer) {
    const x = [...new Uint8Array(buffer)];
    const encodedString = String.fromCharCode.apply(null, x);
    return btoa(encodedString);
}
